import React from "react";
import cx from "clsx";
import NoSsr from "@material-ui/core/NoSsr";
import GoogleFontLoader from "react-google-font-loader";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import { Column, Item } from "@mui-treasury/components/flex";
import { Info, InfoTitle, InfoSubtitle } from "@mui-treasury/components/info";
import Profile from "../components/Profile";

const family = "Rubik";

const useStyles = makeStyles(() => ({
  card: {
    position: "relative",
    borderRadius: 16,
    border: "3px solid  #1e3799",
    padding: 12,
    backgroundColor: "#e5fcfb",
    minWidth: 300,
    minHeight: "38vh",
  },
  smallcard: {
    margin: 0,
    padding: 0,
    position: "relative",
    borderRadius: 16,
    border: "2px solid #bdc3c7",
    backgroundColor: "#e5fcfb",
    minWidth: 135,
    maxHeight: "24vh",
    minHeight: "24vh",
    bottom: 1,
  },
  cardtitle: {
    position: "relative",
    borderRadius: 16,
    border: "2px solid  #1e3799",
    backgroundColor: "#e5fcfb",
    minWidth: 135,
    maxHeight: "8vh",
    minHeight: "8vh",
    bottom: 1,
    backgroundColor: "red",
    backgroundColor: "orange",
    backgroundImage: "linear-gradient(#f1c40f, #e67e22)",
    fontSize: "1.6rem",
    fontFamily: "cfont",
    fontWeight: "900",
    color: "white",
    textAlign: "center !important",
  },
  title1h3: {
    paddingLeft: "3vw",
    fontSize: "1.8rem",
    lineHeight: "4.5vh",
    textStroke: " 0.5px #f39c12",
  },
  title1h1: {
    fontSize: "1.6rem",
    lineHeight: "4.5vh",
    textStroke: " 0.5px #3498db",
  },
  cardtitle1: {
    maxHeight: "8vh",
    backgroundColor: " rgba(43, 174, 254, 1)",
    backgroundImage:
      "linear-gradient( rgba(43, 174, 254, 1),  rgba(40, 154, 200, 1))",
    textStroke: " 0.2px  rgba(23, 68, 166, 1) !important",
  },
  cardimage: {
    height: "auto",
    width: "7vh",
    position: "absolute",
    top: "1vh",
    display: "inline-block",
  },
  learnMore: {
    backgroundColor: "#fff !important",
    color: "#fb703c",
    boxShadow: "0 2px 6px #d0efef",
    borderRadius: 12,
    minWidth: 120,
    minHeight: 42,
    fontFamily: family,
    textTransform: "initial",
    fontSize: "0.875rem",
    fontWeight: 700,
    letterSpacing: 0,
  },
  img: {
    position: "absolute",
    width: "40%",
    bottom: 25,
    right: -6,
    display: "block",
    transform: "rotate(315deg)",
  },
  shell: {
    position: "absolute",
    bottom: 0,
    right: 0,
    transform: "translate(70%, 50%)",
    borderRadius: "50%",
    backgroundColor: "rgba(71, 167, 162, 0.12)",
    padding: "40%",
    "&:before": {
      position: "absolute",
      borderRadius: "50%",
      content: '""',
      display: "block",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      margin: "-16%",
      backgroundColor: "rgba(71, 167, 162, 0.08)",
    },
  },
}));

const InfoStyles = makeStyles((theme) => {
  return {
    title: {
      color: "#fb703c",
      fontFamily: family,
      fontSize: "1.125rem",
      fontWeight: 700,
    },
    subtitle: {
      color: "#48bbb5",
      fontFamily: family,
      fontSize: "0.875rem",
      fontWeight: 500,
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
      color: theme.palette.text.secondary,
      minHeight: "78vh",
      backgroundColor: "transparent",
    },
  };
});

const AwardCard = React.memo(function OfferCard() {
  const styles = useStyles();

  return (
    <>
      <NoSsr>
        <GoogleFontLoader fonts={[{ font: family, weights: [500, 700] }]} />
      </NoSsr>

      <Card className={cx(styles.card)}>
        <Grid container spacing={2}>
          <Grid item xs={6} sm={6}>
            <Paper className={styles.paper} elevation={0}>
              <Card className={cx(styles.cardtitle)}>
                <Column gap={2} mr={0}>
                  <h3 className={styles.title1h1}>淡江大學</h3>
                </Column>
              </Card>
            </Paper>
          </Grid>
          <Grid item xs={6} sm={6}>
            <Paper className={styles.paper} elevation={0}>
              <Card className={cx(styles.cardtitle, styles.cardtitle1)}>
                <Column gap={2} mr={0}>
                  <img
                    className={styles.cardimage}
                    src={require("../images/own-icons/book.png")}
                    alt="book"
                  />
                  <h4 className={styles.title1h3}>學生</h4>
                </Column>
              </Card>
            </Paper>
          </Grid>
        </Grid>
        <Grid container spacing={2}>
          <Grid item xs={6} sm={6}>
            <Paper className={styles.paper} elevation={0}>
              <Card className={cx(styles.smallcard)}>
                <Column gap={0} mr={0}>
                  <Profile name="劉昌岳" image="../images/avatar_male.png" />
                </Column>
              </Card>
            </Paper>
          </Grid>
          <Grid item xs={6} sm={6}>
            <Paper className={styles.paper} elevation={0}>
              <Card className={cx(styles.smallcard)}>
                <Column gap={0} mr={0}>
                  <Profile name="你的盾牌" image="../images/avatar_male.png" />
                </Column>
                <img
                  className={styles.img}
                  alt={""}
                  src={
                    "https://pathwaychurch.life/wp-content/uploads/2018/09/bow-transparent-background-1.png"
                  }
                />
                <div className={styles.shell} />
              </Card>
            </Paper>
          </Grid>
        </Grid>
      </Card>
    </>
  );
});

export default AwardCard;
