import React from "react";
import cx from "clsx";
import NoSsr from "@material-ui/core/NoSsr";
import GoogleFontLoader from "react-google-font-loader";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import { Column, Item } from "@mui-treasury/components/flex";
import { Info, InfoTitle, InfoSubtitle } from "@mui-treasury/components/info";
import Progress from "../components/Progress";
import Typography from "@material-ui/core/Typography";

const family = "Rubik";

const useStyles = makeStyles(() => ({
  card: {
    position: "relative",
    borderRadius: 16,
    border: "3px solid  #1e3799",
    backgroundColor: "#e5fcfb",
    minWidth: 300,
    minHeight: "18vh",
    maxHeight: "18vh",
    marginTop: "10px",
  },
  learnMore: {
    backgroundColor: "#fff !important",
    color: "#fb703c",
    boxShadow: "0 2px 6px #d0efef",
    borderRadius: 12,
    minWidth: 120,
    minHeight: 42,
    fontFamily: family,
    textTransform: "initial",
    fontSize: "0.875rem",
    fontWeight: 700,
    letterSpacing: 0,
  },

  stattitle: {
    fontSize: "1.1rem",
    color: "#1B1464",
    fontFamily: "cfont",
    fontWeight: "500",
    padding: "1px 0px",
    backgroundImage: "linear-gradient(#D980FA, #9980FA)",
    width: "8vw",
    minWidth: "100px",
    margin: "auto",
    borderRadius: 10,
    border: "1px solid #5758BB",
    marginBottom: "0.3vh",
  },

  shell: {
    position: "absolute",
    bottom: 0,
    right: 0,
    transform: "translate(70%, 50%)",
    borderRadius: "50%",
    backgroundColor: "rgba(85, 107, 102, 0.12)",
    padding: "40%",
    "&:before": {
      position: "absolute",
      borderRadius: "50%",
      content: '""',
      display: "block",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      margin: "-16%",
      backgroundColor: "rgba(95, 107, 122, 0.08)",
    },
  },
}));

const titleStyles = makeStyles(() => {
  return {
    title: {
      color: "#fb703c",
      fontFamily: family,
      fontSize: "1.125rem",
      fontWeight: 700,
    },
    subtitle: {
      color: "#48bbb5",
      fontFamily: family,
      fontSize: "0.875rem",
      fontWeight: 500,
    },
  };
});

const TodayProgress = React.memo(function OfferCard() {
  const styles = useStyles();

  return (
    <>
      <NoSsr>
        <GoogleFontLoader fonts={[{ font: family, weights: [500, 700] }]} />
      </NoSsr>
      <Card className={cx(styles.card)}>
        <h5 className={styles.stattitle}>昨天統計</h5>
        <Column gap={2}>
          <Progress value1="35" value2="90" value3="70" />
        </Column>
      </Card>
      <Card className={cx(styles.card)}>
        <h5 className={styles.stattitle}>今天統計</h5>
        <Column gap={2}>
          <Progress value1="25" value2="35" value3="78" />
        </Column>

        <div className={styles.shell} />
      </Card>
    </>
  );
});

export default TodayProgress;
