import React from "react";
import cx from "clsx";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Avatar from "@material-ui/core/Avatar";
import Divider from "@material-ui/core/Divider";
import { useFadedShadowStyles } from "@mui-treasury/styles/shadow/faded";
import { useGutterBorderedGridStyles } from "@mui-treasury/styles/grid/gutterBordered";

const useStyles = makeStyles(({ palette }) => ({
  card: {
    borderRadius: 12,
    maxHeight: "24vh",
    minHeight: "24vh",
    textAlign: "center",
    fontFamily: "cfont",
    backgroundColor: "#ecf0f1 ",
  },
  avatar: {
    width: "16vh",
    height: "16vh",
    margin: "auto",
    marginBottom: 4,
    marginTop: "-10px",
  },
  heading: {
    fontSize: "4vh",
    fontWeight: "bold",
    letterSpacing: "0.5px",
    marginTop: 3,
    marginBottom: 0,
  },
  shell: {
    position: "absolute",
    bottom: 0,
    right: 0,
    transform: "translate(70%, 50%)",
    borderRadius: "50%",
    backgroundColor: "rgba(71, 167, 162, 0.12)",
    padding: "40%",
    "&:before": {
      position: "absolute",
      borderRadius: "50%",
      content: '""',
      display: "block",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      margin: "-16%",
      backgroundColor: "rgba(71, 167, 162, 0.08)",
    },
  },
}));

export const Profile = React.memo(function ProfileCard(props) {
  const styles = useStyles();
  const shadowStyles = useFadedShadowStyles();
  const borderedGridStyles = useGutterBorderedGridStyles({
    borderColor: "rgba(0, 0, 0, 0.08)",
    height: "50%",
  });
  return (
    <Card className={cx(styles.card, shadowStyles.root)}>
      <CardContent>
        <i className="fas fa-user-cog user-conf-btn"></i>
        <i class="fas fa-info info-btn"></i>
        <Avatar className={styles.avatar} src={props.image} />
        <Divider light />
        <h3 className={styles.heading}>{props.name} </h3>
      </CardContent>
    </Card>
  );
});

export default Profile;
