import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { reset_password } from "../actions/auth";
import "../css/resetpassword.css";

const ResetPassword = (props) => {
  const [requestSent, setRequestSent] = useState(false);

  const [formData, setFormData] = useState({
    email: "",
  });

  const { email } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    props.reset_password(email);
    setRequestSent(true);
  };

  if (requestSent) return <Redirect to="/" />;
  return (
    <div className="container mt-5">
      <div className="row h-100">
        <div className="col-6 my-auto mx-auto">
          <div className="card reset-card mx-auto my-auto">
            <div className="card-body">
              <h1>重設密碼</h1>
              <form onSubmit={(e) => onSubmit(e)}>
                <div className="form-group">
                  <div class="form-control">
                    <i class="fas fa-envelope"></i>
                    <input
                      type="text"
                      placeholder="您的 Email"
                      name="email"
                      value={email}
                      onChange={(e) => onChange(e)}
                      required
                    />
                  </div>
                </div>
                <button className="btn button2 btn-primary" type="submit">
                  寄送
                </button>
                <a className="btn button3 btn-warning" href="/login">
                  返回登入頁面
                </a>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { reset_password })(ResetPassword);
