import React from "react";
import "../css/lesson-block.css";

const LessonBlock = () => {
  return (
    <div class="title-section">
      <div class="lesson-div lesson1">
        <div class="lesson-num">
          <h1>1</h1>
        </div>
        <div class="lesson-data">
          <div class="data-top">
            <h1 class="lesson-title">運動飲食</h1>
            <div class="lesson-btns">
              <h3>完成學習</h3>
              <div class="tick-group">
                <img src="../images/own-icons/rect-violet.png" alt="" />
                <img
                  src="../images/own-icons/tick-violet.png"
                  alt=""
                  id="tick-v1"
                />
              </div>
              <h3>特殊獎勵</h3>
              <div class="tick-group">
                <img src="images/own-icons/rect-violet.png" alt="" />
                <img
                  src="../images/own-icons/tick-violet.png"
                  alt=""
                  id="tick-v2"
                />
              </div>
            </div>
          </div>
          <div class="data-bottom">
            <progress max="100" value="80"></progress>
          </div>
        </div>
      </div>
    </div>
  );
};

export default LessonBlock;
