import React, { useState } from "react";
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import { signup } from "../actions/auth";
import "../css/signup.css";

const Signup = ({ signup, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    email: "",
    username: "",
    password: "",
    re_password: "",
  });

  const [accountCreated, setAccountCreated] = useState(false);

  const { email, username, password, re_password } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    if (password === re_password) {
      signup({ email, username, password, re_password });
      setAccountCreated(true);
    }
  };

  if (isAuthenticated) return <Redirect to="/" />;
  if (accountCreated) return <Redirect to="login" />;

  return (
    <div className="container-fluid">
      <div className="row h-100">
        <div className="mx-auto my-auto">
          <div className="card signup-card text-center">
            <div className="card-body">
              <h2>註冊</h2>
              <form onSubmit={(e) => onSubmit(e)}>
                <div class="form-control">
                  <i class="fas fa-envelope"></i>
                  <input
                    type="text"
                    placeholder="您的 Email"
                    name="email"
                    value={email}
                    onChange={(e) => onChange(e)}
                    required
                  />
                </div>
                <div class="form-control">
                  <i class="fas fa-user"></i>
                  <input
                    type="text"
                    placeholder="用戶名"
                    name="username"
                    value={username}
                    onChange={(e) => onChange(e)}
                    required
                  />
                </div>

                <div class="form-control">
                  <i class="fas fa-key"></i>
                  <input
                    type="password"
                    placeholder="密碼"
                    value={password}
                    onChange={(e) => onChange(e)}
                    minLength="8"
                    required
                  />
                </div>
                <div class="form-control">
                  <i class="fas fa-key"></i>
                  <input
                    type="password"
                    placeholder="確認密碼"
                    name="re_password"
                    value={re_password}
                    onChange={(e) => onChange(e)}
                    minLength="8"
                    required
                  />
                </div>

                <button className="btn btn-primary" type="submit">
                  註冊
                </button>
              </form>
              <p className="mt-3">
                已經有帳號了？ <Link to="/login">登入</Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { signup })(Signup);
