import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { reset_password_confirm } from "../actions/auth";

const ResetPasswordConfirm = (props) => {
  const [requestSent, setRequestSent] = useState(false);

  const [formData, setFormData] = useState({
    new_password: "",
    re_new_password: "",
  });

  const { new_password, re_new_password } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    const uid = props.match.params.uid;
    const token = props.match.params.token;

    props.reset_password_confirm(uid, token, new_password, re_new_password);
    setRequestSent(true);
  };

  if (requestSent) return <Redirect to="/" />;
  return (
    <div className="container-fluid">
      <div className="row h-100">
        <div className=" my-auto mx-auto my-auto">
          <div className="card login-card mx-auto text-center p-5">
            <h3>設置新密碼 </h3> <br />
            <form onSubmit={(e) => onSubmit(e)}>
              <div className="form-group">
                <div class="input-field">
                  <i class="fas fa-lock"></i>
                  <input
                    type="password"
                    placeholder="新密碼"
                    name="new_password"
                    value={new_password}
                    onChange={(e) => onChange(e)}
                    minLength="8"
                    required
                  />
                </div>
              </div>
              <div className="form-group">
                <div class="input-field">
                  <i class="fas fa-lock"></i>
                  <input
                    type="password"
                    placeholder="確認新密碼"
                    name="re_new_password"
                    value={re_new_password}
                    onChange={(e) => onChange(e)}
                    minLength="8"
                    required
                  />
                </div>
              </div>{" "}
              <br />
              <button className="btn button btn-primary" type="submit">
                重設密碼
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default connect(null, { reset_password_confirm })(ResetPasswordConfirm);
