import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";

import LinearProgress from "@material-ui/core/LinearProgress";

const CalorieProgress = withStyles((theme) => ({
  root: {
    height: 16,
    marginTop: 0,
    marginBottom: 5,
    borderRadius: 20,
  },
  colorPrimary: {
    backgroundColor:
      theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
  },
  bar: {
    borderRadius: 20,
    backgroundColor: " #53f22d",
  },
}))(LinearProgress);

const SleepProgress = withStyles((theme) => ({
  root: {
    height: 16,
    marginTop: 0,
    marginBottom: 5,
    borderRadius: 20,
  },
  colorPrimary: {
    backgroundColor:
      theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
  },
  bar: {
    borderRadius: 20,
    backgroundColor: " #f0932b",
  },
}))(LinearProgress);

const StepsProgress = withStyles((theme) => ({
  root: {
    height: 16,
    marginTop: 0,
    marginBottom: 5,
    borderRadius: 20,
  },
  colorPrimary: {
    backgroundColor:
      theme.palette.grey[theme.palette.type === "light" ? 200 : 700],
  },
  bar: {
    borderRadius: 20,
    backgroundColor: " ff1b13",
  },
}))(LinearProgress);

const useStyles = makeStyles({
  root: {
    flexGrow: 1,
  },
  progtitle: {
    width: "5vw",
    textAlign: "right",
    marginBottom: "8px",
    minWidth: "50px",
    fontWeight: "600",
    lineHeight: "4vh",
  },
  progtable: {
    width: "100%",
    marginTop: "-5px",
    marginLeft: "-5px",
  },
});

export default function Progress(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <table className={classes.progtable}>
        <tr>
          <td className={classes.progtitle}>卡路里</td>
          <td>
            <CalorieProgress
              className={classes.bar}
              variant="determinate"
              value={props.value1}
            />
          </td>
        </tr>
        <tr>
          <td className={classes.progtitle}>步數</td>
          <td>
            {" "}
            <StepsProgress
              className={classes.bar}
              variant="determinate"
              value={props.value2}
            />
          </td>
        </tr>
        <tr>
          <td className={classes.progtitle}>睡眠量</td>
          <td>
            <SleepProgress
              className={classes.bar}
              variant="determinate"
              value={props.value3}
            />
          </td>
        </tr>
      </table>
    </div>
  );
}
